import React, { Component } from 'react';
import { Container } from 'reactstrap';
import Header from '../share/header';
import Footer from '../share/footer';
import './privacy.css';

class Privacy extends Component {
  render() {
    return (
      <div>
        <Header />
        <Container className="container-privacy">
          <h1>Privacy Policy</h1>
          <p>
            This privacy policy has been compiled to better serve those who are
            concerned with how their 'Personally identifiable information' (PII)
            is being used online. PII, as used in US privacy law and information
            security, is information that can be used on its own or with other
            information to identify, contact, or locate a single person, or to
            identify an individual in context. Please read our privacy policy
            carefully to get a clear understanding of how we collect, use,
            protect or otherwise handle your Personally Identifiable Information
            in accordance with our website.
          </p>
          <h2>
            What personal information do we collect from the people that visit
            our blog, website or app?
          </h2>
          <p>
            When ordering or registering on our site, as appropriate, you may be
            asked to enter your name, email address, phone number, credit card
            information or other details to help you with your experience.
          </p>
          <h2>When do we collect information?</h2>
          <p>
            We collect information from you when you register on our site, place
            an order or enter information on our site.
          </p>
          <h2>How do we use your information?</h2>
          <p>
            We may use the information we collect from you when you register,
            make a purchase, sign up for our newsletter, respond to a survey or
            marketing communication, surf the website, or use certain other site
            features in the following ways:<br />
            <br />
            • To personalize user's experience and to allow us to deliver the
            type of content and product offerings in which you are most
            interested.<br />
            • To improve our website in order to better serve you.<br />
            • To allow us to better service you in responding to your customer
            service requests.<br />
            • To quickly process your transactions.<br />
            • To send periodic emails regarding your order or other products and
            services.
          </p>
          <h2>How do we protect visitor information?</h2>
          <p>
            Our website is scanned on a regular basis for security holes and
            known vulnerabilities in order to make your visit to our site as
            safe as possible.
          </p>
          <p>We do not use Malware Scanning.</p>
          <p>
            Your personal information is contained behind secured networks and
            is only accessible by a limited number of persons who have special
            access rights to such systems, and are required to keep the
            information confidential. In addition, all sensitive/credit
            information you supply is encrypted via Secure Socket Layer (SSL)
            technology.
          </p>
          <p>
            We implement a variety of security measures when a user places an
            order enters, submits, or accesses their information to maintain the
            safety of your personal information.
          </p>
          <p>
            All transactions are processed through a gateway provider and are
            not stored or processed on our servers.
          </p>
          <h2>Do we use 'cookies'?</h2>
          <p>
            Yes. Cookies are small files that a site or its service provider
            transfers to your computer's hard drive through your Web browser (if
            you allow) that enables the site's or service provider's systems to
            recognize your browser and capture and remember certain information.
            For instance, we use cookies to help us remember and process the
            items in your shopping cart. They are also used to help us
            understand your preferences based on previous or current site
            activity, which enables us to provide you with improved services. We
            also use cookies to help us compile aggregate data about site
            traffic and site interaction so that we can offer better site
            experiences and tools in the future.
          </p>
          <p>
            We use cookies to:<br />
            • Help remember and process the items in the shopping cart.<br />
            • Understand and save user's preferences for future visits.<br />
            • Keep track of advertisements.<br />
            • Compile aggregate data about site traffic and site interactions in
            order to offer better site experiences and tools in the future. We
            may also use trusted third-party services that track this
            information on our behalf.
          </p>
          <p>
            You can choose to have your computer warn you each time a cookie is
            being sent, or you can choose to turn off all cookies. You do this
            through your browser (like Internet Explorer) settings. Each browser
            is a little different, so look at your browser's Help menu to learn
            the correct way to modify your cookies.
          </p>
          <h2>If users disable cookies in their browser:</h2>
          <p>
            If you disable cookies off, some features will be disabled It will
            turn off some of the features that make your site experience more
            efficient and some of our services will not function properly.
          </p>
          <p>
            However, you can still place orders by contacting customer service.
          </p>
          <h2>Third-party disclosure</h2>
          <p>
            We do not sell, trade, or otherwise transfer to outside parties your
            personally identifiable information unless we provide users with
            advance notice. This does not include website hosting partners and
            other parties who assist us in operating our website, conducting our
            business, or serving our users, so long as those parties agree to
            keep this information confidential. We may also release information
            when it's release is appropriate to comply with the law, enforce our
            site policies, or protect ours or others' rights, property, or
            safety.{' '}
          </p>
          <p>
            However, non-personally identifiable visitor information may be
            provided to other parties for marketing, advertising, or other uses.
          </p>
          <h2>Third-party links</h2>
          <p>
            Occasionally, at our discretion, we may include or offer third-party
            products or services on our website. These third-party sites have
            separate and independent privacy policies. We therefore have no
            responsibility or liability for the content and activities of these
            linked sites. Nonetheless, we seek to protect the integrity of our
            site and welcome any feedback about these sites.
          </p>
          <h2>Google</h2>
          <p>
            Google's advertising requirements can be summed up by Google's
            Advertising Principles. They are put in place to provide a positive
            experience for users.
            https://support.google.com/adwordspolicy/answer/1316548?hl=en{' '}
          </p>
          <p>We use Google AdSense Advertising on our website.</p>
          <p>
            Google, as a third-party vendor, uses cookies to serve ads on our
            site. Google's use of the DART cookie enables it to serve ads to our
            users based on previous visits to our site and other sites on the
            Internet. Users may opt-out of the use of the DART cookie by
            visiting the Google Ad and Content Network privacy policy.
          </p>
          <p>
            We have implemented the following:<br />
            • Remarketing with Google AdSense<br />
            • Google Display Network Impression Reporting<br />
            • Demographics and Interests Reporting<br />
            • DoubleClick Platform Integration
          </p>
          <p>
            We along with third-party vendors, such as Google use first-party
            cookies (such as the Google Analytics cookies) and third-party
            cookies (such as the DoubleClick cookie) or other third-party
            identifiers together to compile data regarding user interactions
            with ad impressions and other ad service functions as they relate to
            our website.
          </p>
          <p>
            Opting out:<br />
            Users can set preferences for how Google advertises to you using the
            Google Ad Settings page. Alternatively, you can opt out by visiting
            the Network Advertising initiative opt out page or permanently using
            the Google Analytics Opt Out Browser add on.
          </p>
          <h2>California Online Privacy Protection Act</h2>
          <p>
            CalOPPA is the first state law in the nation to require commercial
            websites and online services to post a privacy policy. The law's
            reach stretches well beyond California to require a person or
            company in the United States (and conceivably the world) that
            operates websites collecting personally identifiable information
            from California consumers to post a conspicuous privacy policy on
            its website stating exactly the information being collected and
            those individuals with whom it is being shared, and to comply with
            this policy. - See more at:
            http://consumercal.org/california-online-privacy-protection-act-caloppa/#sthash.0FdRbT51.dpuf
          </p>
          <p>
            According to CalOPPA we agree to the following:<br />
            Users can visit our site anonymously.<br />
            Once this privacy policy is created, we will add a link to it on our
            home page or as a minimum on the first significant page after
            entering our website.<br />
            Our Privacy Policy link includes the word 'Privacy' and can be
            easily be found on the page specified above.
          </p>
          <p>
            Users will be notified of any privacy policy changes:<br />
            • On our Privacy Policy Page<br />
            Users are able to change their personal information:<br />
            • By emailing us<br />
            • By logging in to their account
          </p>
          <h2>How does our site handle do not track signals?</h2>
          <p>
            We honor do not track signals and do not track, plant cookies, or
            use advertising when a Do Not Track (DNT) browser mechanism is in
            place.
          </p>
          <h2>Does our site allow third-party behavioral tracking?</h2>
          <p>
            It's also important to note that we allow third-party behavioral
            tracking
          </p>
          <h2>COPPA (Children Online Privacy Protection Act)</h2>
          <p>
            When it comes to the collection of personal information from
            children under 13, the Children's Online Privacy Protection Act
            (COPPA) puts parents in control. The Federal Trade Commission, the
            nation's consumer protection agency, enforces the COPPA Rule, which
            spells out what operators of websites and online services must do to
            protect children's privacy and safety online.
          </p>
          <p>We do not specifically market to children under 13.</p>
          <h2>Fair Information Practices</h2>
          <p>
            The Fair Information Practices Principles form the backbone of
            privacy law in the United States and the concepts they include have
            played a significant role in the development of data protection laws
            around the globe. Understanding the Fair Information Practice
            Principles and how they should be implemented is critical to comply
            with the various privacy laws that protect personal information.
          </p>
          <p>
            In order to be in line with Fair Information Practices we will take
            the following responsive action, should a data breach occur:<br />
            • Within 7 business days<br />
            We will notify the users via in-site notification<br />
            • Within 7 business days
          </p>
          <p>
            We also agree to the Individual Redress Principle, which requires
            that individuals have a right to pursue legally enforceable rights
            against data collectors and processors who fail to adhere to the
            law. This principle requires not only that individuals have
            enforceable rights against data users, but also that individuals
            have recourse to courts or government agencies to investigate and/or
            prosecute non-compliance by data processors.
          </p>
          <h2>CAN SPAM Act</h2>
          <p>
            The CAN-SPAM Act is a law that sets the rules for commercial email,
            establishes requirements for commercial messages, gives recipients
            the right to have emails stopped from being sent to them, and spells
            out tough penalties for violations.
          </p>
          <p>We collect your email address in order to:</p>
          <p>To be in accordance with CANSPAM we agree to the following:</p>
          <p>
            If at any time you would like to unsubscribe from receiving future
            emails, you can email us and we will promptly remove you from ALL
            correspondence.
          </p>
          <h2>Contacting Us</h2>
          <p>
            If there are any questions regarding this privacy policy you may
            contact us using the information below.
          </p>
          <p>
            TheNextDive.com<br />
            364 E Main Street #1001<br />
            Middletown, Delaware 19709<br />
            USA<br />
            contact@thenextdive.com
          </p>
          <Footer />
        </Container>
      </div>
    );
  }
}

export default Privacy;



